.board-section {
  background: #f9f9fb;
  top: 30px;
  padding: 3rem 2rem;

  .board-container {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 2rem;
    align-items: start;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
  }

  .board-left {
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      width: 100%;
      max-width: 300px;
      border-radius: 8px;
      box-shadow: 0 8px 20px rgba(0,0,0,0.1);
      animation: fadeInLeft 1s ease-in-out;
    }

    .download-btn {
      margin-top: 1rem;
      color: #b45309;
      font-weight: 600;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }

