.board-middle {
  animation: fadeInUp 1s ease-in-out;

  .title {
    font-size: 1.6rem;
    font-weight: 700;
    margin-bottom: 0.8rem;
    color: #0b4a91;
    position: relative;
    display: inline-block; // makes ::after align with text width

    &::after {
      content: "";
      position: absolute;
      left: 48%;               // start from center
      transform: translateX(-50%); // keep it centered
      bottom: -6px;            // space below text
      width: 60px;
      height: 3px;
      background: #f4a629;
    }
  }

  h3 {
    font-size: 1.3rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .quote {
    font-style: italic;
    color: #444;
    margin-bottom: 1rem;
  }

  p {
    margin-bottom: 1rem;
    line-height: 1.6;
    color: #555;
  }

  .read-more {
    display: inline-block;
    margin-top: 1rem;
    font-size: 0.95rem;
    color: #b45309;
    text-decoration: none;
    font-weight: 600;
    &:hover {
      text-decoration: underline;
    }
  }
}


  .board-right {
    animation: fadeInRight 1s ease-in-out;
    .title {
      font-size: 1.6rem;
      font-weight: 700;
      margin-bottom: 1rem;
      color: #0b4a91;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        left: 40%;
        display: block;
        width: 80px;
        height: 3px;
        background: #f4a629;
        margin-top: 6px;
      }
    }

    .speeches {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        display: flex;
        gap: 1rem;
        margin-bottom: 1.5rem;

        .date {
          font-size: 1.2rem;
          font-weight: 700;
          color: #0b4a91;
          small {
            display: block;
            font-size: 0.8rem;
            color: #777;
          }
        }

        p {
          margin: 0;
          font-size: 0.95rem;
          color: #333;
        }
      }
    }

    .read-more {
      font-size: 0.95rem;
      color: #b45309;
      text-decoration: none;
      font-weight: 600;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

/* Animations */
@keyframes fadeInLeft {
  from { opacity: 0; transform: translateX(-30px); }
  to { opacity: 1; transform: translateX(0); }
}
@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}
@keyframes fadeInRight {
  from { opacity: 0; transform: translateX(30px); }
  to { opacity: 1; transform: translateX(0); }
}
