.grid-clm{margin-top:200px}.grid-clm .board-section{background:#f9f9fb;margin-top:70px;padding:1rem 2rem}.grid-clm .board-section .board-container{display:grid;grid-template-columns:1fr 2fr;gap:2rem;text-align:start}@media(max-width: 992px){.grid-clm .board-section .board-container{grid-template-columns:1fr;text-align:center}}.grid-clm .board-section .board-left{display:flex;flex-direction:column;align-items:center}.grid-clm .board-section .board-left img{width:100%;max-width:200px;border-radius:8px;box-shadow:0 8px 20px rgba(0,0,0,.1);animation:fadeInLeft 1s ease-in-out;bottom:10px}.grid-clm .board-section .board-left .download-btn{margin-top:1rem;color:#b45309;font-weight:600;text-decoration:none}.grid-clm .board-section .board-left .download-btn:hover{text-decoration:underline}.grid-clm .board-section .board-left h3{top:10px;font-size:1.4rem;font-weight:700;color:#0b4a91;margin-bottom:.3rem}.grid-clm .board-section .board-left .role{display:inline-block;font-size:1rem;font-weight:500;color:#555;background:#f4f6fa;padding:.3rem .8rem;border-radius:20px}.grid-clm .board-section .board-middle{animation:fadeInUp 1s ease-in-out}.grid-clm .board-section .board-middle .title{font-size:1.6rem;font-weight:700;margin-bottom:.8rem;color:#0b4a91;position:relative;justify-content:start;display:inline-block}.grid-clm .board-section .board-middle .title::after{content:"";position:absolute;left:48%;transform:translateX(-50%);bottom:-6px;width:60px;height:3px;background:#f4a629}.grid-clm .board-section .board-middle h3{font-size:1.3rem;font-weight:600;margin-bottom:1rem}.grid-clm .board-section .board-middle .quote{font-style:italic;color:#444;margin-bottom:1rem}.grid-clm .board-section .board-middle p{font-size:1.3rem;margin-bottom:1rem;line-height:1.6;color:#555}.grid-clm .board-section .board-middle .read-more{display:inline-block;margin-top:1rem;font-size:1rem;font-weight:600;color:#b45309;text-decoration:none;position:relative;transition:all .3s ease}.grid-clm .board-section .board-middle .read-more::after{content:"";position:absolute;left:0;bottom:-2px;width:0%;height:2px;background-color:#b45309;transition:width .3s ease}.grid-clm .board-section .board-middle .read-more:hover{color:#92400e;cursor:pointer;text-decoration:none}.grid-clm .board-section .board-middle .read-more:hover::after{width:100%}.grid-clm .board-section .board-middle .read-more::before{content:"â†’";opacity:0;margin-right:6px;transition:all .3s ease}.grid-clm .board-section .board-middle .read-more:hover::before{opacity:1;margin-right:10px}.grid-clm .board-section .board-right{animation:fadeInRight 1s ease-in-out}.grid-clm .board-section .board-right .title{font-size:1.6rem;font-weight:700;margin-bottom:1rem;color:#0b4a91;position:relative}.grid-clm .board-section .board-right .title::after{content:"";position:absolute;left:40%;display:block;width:80px;height:3px;background:#f4a629;margin-top:6px}.grid-clm .board-section .board-right .speeches{list-style:none;padding:0;margin:0}.grid-clm .board-section .board-right .speeches li{display:flex;gap:1rem;margin-bottom:1.5rem}.grid-clm .board-section .board-right .speeches li .date{font-size:1.2rem;font-weight:700;color:#0b4a91}.grid-clm .board-section .board-right .speeches li .date small{display:block;font-size:.8rem;color:#777}.grid-clm .board-section .board-right .speeches li p{margin:0;font-size:.95rem;color:#333}.grid-clm .board-section .board-right .read-more{font-size:.95rem;color:#b45309;text-decoration:none;font-weight:600}.grid-clm .board-section .board-right .read-more:hover{text-decoration:underline}@keyframes fadeInLeft{from{opacity:0;transform:translateX(-30px)}to{opacity:1;transform:translateX(0)}}@keyframes fadeInUp{from{opacity:0;transform:translateY(30px)}to{opacity:1;transform:translateY(0)}}@keyframes fadeInRight{from{opacity:0;transform:translateX(30px)}to{opacity:1;transform:translateX(0)}}